import React, { Component } from 'react'
import './App.css'

class Skills extends Component {
  render() {
    return (
      <div className="skills">
        <h1>SKILLS</h1>
        <div className='skills-data'>
          <div>
            <i className="fab fa-html5"> HTML5</i>
          </div>
          <div>
            <i className="fab fa-css3"> CSS3</i>
          </div>
          <div>
            <i className="fab fa-js"> JAVASCRIPT</i>
          </div>
          <div>
            <i className="fas fa-project-diagram"> OBJECT ORIENTED</i>
          </div>
          <div>
            <i className="fas fa-dharmachakra"> JASMINE</i>
          </div>
          <div>
            <i className="fab fa-gulp"> GULP</i>
          </div>
          <div>
            <i className="fab fa-git"> GIT</i>
          </div>
          <div>
            <i className="fab fa-node"> NODE.JS</i>
          </div>
          <div>
            <i className="fas fa-terminal"> COMMAND LINE</i>
          </div>
          <div>
            <i className="fas fa-cogs"> SERVICE WORKERS</i>
          </div>
          <div>
            <i className="fas fa-file"> MANIFEST</i>
          </div>
          <div>
            <i className="fab fa-react"> REACT.JS</i>
          </div>
        </div>
      </div>
    )
  }
}

export default Skills;
