import React, { Component } from 'react'
import './App.css'

class Header extends Component {
  render() {
    return (
      <header>
        <h1 className='heading'>DILRAJ SINGH</h1>
        <h1 className='title'>FRONT-END WEB DEVELOPER</h1>
        <div className='contact'>
          <i className="fas fa-envelope email" onClick={() => window.open("mailto:dilrajkehra@gmail.com")}> Email</i>
          <i className="fab fa-linkedin linkedin" onClick={() => window.open("https://www.linkedin.com/in/dilrajsinghkhehra")}> LinkedIn</i>
          <i className="fab fa-instagram instagram" onClick={() => window.open("https://www.instagram.com/dilrajsinghkhehra/")}> Instagram</i>
          <i className="fab fa-github github" onClick={() => window.open("https://github.com/SinghDilraj")}> Github</i>
        </div>
      </header>
    )
  }
}

export default Header;
